@import "color";
.contact{
    width: 100%;
    height: 100vh;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;

    >main{
        background-color:rgb(167, 223, 209);
        height: 30rem;
        width: 70%;
        padding: 3rem;
        border-radius: 0 200px 200px 0;
        >form{
            margin: 5rem;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            >div{
                height: 3rem;
                display: flex;
                align-items: center;
                justify-content: flex-end;
                width: 60%;
                margin: 0.5rem;
                >label{
                    font: 400 1.2rem "Roboto";
                    width: 20%;
                    margin: 0 1rem;
                }
                >input{
                    align-self: stretch;
                    width: 80%;
                    padding: 1rem;
                    border-radius: 5px;
                    border: 1px solid rgba(123,123,123,0.6);
                    outline: none;
                }
            }
            >button{
                margin: 3rem 0;
                padding: 0.7rem;
                border: none;
                border-radius: 5px;
                background-color:white;
                color: black;
                width: 200px;
                font: 600 1rem red;
                &:hover{
                    opacity: 0.9;
                }
            }
        }

    }
}